import { EnvService } from "./../../services/env.service";
import { NavController, IonRefresher } from "@ionic/angular";
import { User } from "./../../models/user";
import { ProviderService } from "./../../services/provider.service";
import { Component, OnInit } from "@angular/core";
import { Provider } from "src/app/models/provider";
import { map } from "rxjs/operators";
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: "app-usr-favorite-providers",
  templateUrl: "./usr-favorite-providers.page.html",
  styleUrls: ["./usr-favorite-providers.page.scss"],
})
export class UsrFavoriteProvidersPage implements OnInit {
  public getName = User.getName;
  refresher:IonRefresher = null;
  constructor(
    public providerService: ProviderService,
    private navController: NavController,
    public env: EnvService
  ) {}
  navigateToProvider(provider: Provider) {
    this.providerService.setSelectedProvider(provider);

    this.navController.navigateForward(["provider/" + provider.id]);
  }
  
  refresh(event) {
    this.refresher = event.target;
    this.providerService.favoriteProvidersShared.request();
  }
  ionViewWillEnter() {
  }
  ngOnInit() {
    this.providerService.favoriteProvidersShared.getUpdateNotifier(this).subscribe(()=>{
      this.refresher && this.refresher.complete();
    })
    this.providerService.favoriteProvidersShared.request();
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    
  }
}
